import React, { PureComponent } from "react";
import Dropdown from 'react-bootstrap/Dropdown';
import icons from 'glyphicons';
import {getQueryStringValue} from '../service/utils';


class SimpleMenu extends PureComponent {

  menuListe = [
            ["ManageData/ManageData" ,"Manage your data"],
            ["ManageForms/ManageForms"   ,"Manage your forms"],
            ["Movies/Movies" ,"Finding movies"],

            ["FormatJson/FormatJson" , "Example : How to format Json"],
            ["DynamicForm/JsonFormExample" ,"Example : Dynamic form"],
  ];

  constructor(props) {
    super(props);
    this.state = {
       menuItemText : ""
    };

    let menuKey =  getQueryStringValue('menu');
    console.log('menuKey', menuKey);
    
    let menu = this.menuListe.find(element => element[0] === menuKey );
    if(menu) {
      this.state.menuItemText = menu[1];
    }
    console.log('baseurl', window.location.href.split('?')[0]);
  };



  render() {
    return (<div className="container  py-2">

      <div className="row">
        
        {icons.rabbitFace}
        <span className="px-1">Welcome !!!</span>
		<a target="__BLANK" 
							href="https://github.com/prawinn555/reactdemo/wiki" 
							className="px-1" > please see Wiki</a>
        {icons.rabbit}


        <Dropdown className="px-1">
          <Dropdown.Toggle variant="primary" id="dropdown-basic">
            Menu
          </Dropdown.Toggle>

          <Dropdown.Menu>
            { this.menuListe.map ( (v,index) => 
              <Dropdown.Item href="#" onClick={ () => window.location.href = window.location.href.split('?')[0] + "?menu=" + v[0] } 
                   key={v[0]}>{v[1]}</Dropdown.Item>
            )}
          </Dropdown.Menu>
        </Dropdown>

         <header className="px-3">{this.state.menuItemText}</header>
      </div>
    </div>);
  }
}
export default SimpleMenu;
